import React from 'react';

export function PageAll() {
  let datas = ['ca', 'tim', 'gan', 'phi', 'mui', 'mm']
  let handle = (val) => alert(val)
  return (
    <div>
      <h2>Hello </h2>
      <ul>
        {datas.map((data)=> <li onClick={()=> {
          handle(data)
        }}>{data}</li>)}
      </ul>
    </div>
  );
}
